import React from 'react';

import Banner from './Banner';
import { LinkedInButton, TwitterButton } from './SocialButton';

/**
 * A hero banner
 */
export default class Hero extends React.Component {
  static MAX_HEIGHT = 500;
  static MIN_HEIGHT = 100;
  static SCROLL_CUTOFF = Hero.MAX_HEIGHT - Hero.MIN_HEIGHT;
  static INIT_GREETING_WIDTH = 0.5;

  constructor(props) {
    super(props);

    this.state = {
      top: 0,
      height: Hero.MAX_HEIGHT,
    }
    
    this.handleScroll = this.handleScroll.bind(this);
  }

  componentWillMount() {
    this.scrollListener = document.addEventListener('scroll', this.handleScroll);
  }

  componentWillUnmount() {
    document.removeEventListener('scroll', this.scrollListener);
  }

  handleScroll() {
    const top = window.scrollY;
    
    this.setState({
      top: top < Hero.SCROLL_CUTOFF ? top : Hero.SCROLL_CUTOFF,
      height: top < Hero.SCROLL_CUTOFF ? Hero.MAX_HEIGHT - top : Hero.MAX_HEIGHT - Hero.SCROLL_CUTOFF,
    });
  }
  
  render() {
    const { name, headshot } = this.props;
    const headshotSize = this.state.height * 0.8;
    const percent = this.state.top / Hero.SCROLL_CUTOFF;
    const greetingWidth = 25 + (Hero.INIT_GREETING_WIDTH - 0.25) * 100 * (1 - percent);
    const greetingY = -100 * (1 - percent);

    return <Banner className='hero' style={{ top: -Hero.SCROLL_CUTOFF }}>
      <div className='hero-content' style={{ marginTop: this.state.top }}>
        <img src={headshot} alt={name} style={{
          width: headshotSize,
          height: headshotSize,
        }}/>
        <div className='conversation' style={{
          height: this.state.height,
        }}>
          <div className='greeting speech-bubble' style={{
            width: greetingWidth + '%',
            marginTop: greetingY,
          }}>
            <p>Hi, I'm {name}</p>
          </div>
          <div className='action speech-bubble'>
            <p>Continue the conversation</p>
            <div>
              <LinkedInButton/>
              <TwitterButton/>
            </div>
          </div>
        </div>
      </div>
    </Banner>;
  }
}
